body{
    direction: rtl;
    background-color: #ffffff;
    font-family: Helvetica;
}

.clickable{
    cursor: pointer;
}

.logo {
    max-height: 100px;
}

a {
    // color: #063837;
    color: black;
    text-decoration: none;
}

img{
    border-radius: inherit;
}

.shadowed{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.red {
    background-color: #fb4a59;
    * {
        color: white;
    }
}
